import AccountStatusService from '@/services/account/AccountStatusService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let AccountStatusTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultAccountStatusTableFilter: {
      type: String,
      default: null
    },
    defaultAccountStatusTableOrderBy: {
      type: String,
      default: null
    },
    statusBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      accountStatusTableData: [],
      accountStatusTableFilterTimer: null,
      accountStatusTableFilter: this.defaultAccountStatusTableFilter,
      accountStatusTableOrderBy: this.defaultAccountStatusTableOrderBy,
      accountStatusTableContainer: 'loading-container',
      accountStatusTableBusy: false,
      accountStatusTableTotalRows: 0,
      accountStatusTableCurrentPage: 1,
      accountStatusTablePerPage: 10
    }
  },
  computed: {
    accountStatusTableNoDataText: function () {
      return this.accountStatusTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    accountStatusTableTotalPages: function () {
      return Math.ceil(this.accountStatusTableTotalRows / this.accountStatusTablePerPage)
    },
    accountStatusTablePagingInfo: function () {
      return this.pagingInfo(this.accountStatusTableCurrentPage, this.accountStatusTablePerPage, this.accountStatusTableTotalRows)
    }
  },
  methods: {
    loadAccountStatusTableData (loadingContainerId) {
      let self = this
      this.accountStatusTableBusy = true
      this.accountStatusTableContainer = loadingContainerId || this.accountStatusTableContainer
      this.startLoading(this.accountStatusTableContainer)
      AccountStatusService.accountStatuses({
        pageIndex: this.accountStatusTableCurrentPage - 1,
        pageSize: this.accountStatusTablePerPage,
        filter: this.accountStatusTableFilter,
        orderByField: this.accountStatusTableOrderBy,
        statusBehaviourEnumId: this.statusBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.accountStatusTableContainer)
        self.accountStatusTableBusy = false
        self.accountStatusTableTotalRows = response.data.totalRows
        self.accountStatusTableData = response.data.accountStatusList || []
      }).catch(function (error) {
        self.stopLoading(self.accountStatusTableContainer)
        self.accountStatusTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshAccountStatusTable () {
      this.loadAccountStatusTableData()
    },
    filterAccountStatusTable (filter) {
      let self = this
      if (this.accountStatusTableFilter !== filter) {
        clearTimeout(this.accountStatusTableFilterTimer)
        this.accountStatusTableFilterTimer = setTimeout(() => {
          self.accountStatusTableFilter = filter
          self.loadAccountStatusTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.accountStatusTableFilterTimer)
      }
    },
    orderAccountStatusTable (orderBy, active) {
      this.accountStatusTableOrderBy = (active ? '' : '-') + orderBy
      this.loadAccountStatusTableData()
    }
  },
  watch: {
    accountStatusTableCurrentPage: function () {
      this.refreshAccountStatusTable()
    },
    accountStatusTablePerPage: function () {
      this.refreshAccountStatusTable()
    },
    statusBehaviourEnumId: function () {
      this.refreshAccountStatusTable()
    }
  }
}

export default AccountStatusTableMixin
